@import url(https://fonts.googleapis.com/css2?family=Jost:wght@500;700&display=swap);
*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}.has-padding-top{padding-top:20px}.has-padding-bottom{padding-bottom:20px}.has-margin-top{margin-top:20px}.has-margin-bottom{margin-bottom:20px}.nudge-top{margin-top:10px}.nudge-bottom{margin-bottom:10px}.hidden{display:none !important}html,body{-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:100%;background-color:#edf2f4;overflow-x:hidden}body{color:#2b2d42;font-family:'Jost', sans-serif;font-size:16px;font-weight:500;line-height:1.5;margin:0;min-height:100vh}.container{width:100%;margin:0 auto;max-width:1080px}main.container{margin-top:1.5rem;width:calc(100% - 2rem);padding:0.5rem 1rem}button{padding:1rem 2rem;cursor:pointer;background-color:tomato;border:0;color:white;font-weight:700;font-size:1rem}header{background-color:white;border-bottom:1px solid #ccc;width:100%}header .container{display:flex;-moz-justify-content:center;-ms-justify-content:center;justify-content:center;-ms-flex-pack:center;padding:0.5rem 1rem;width:calc(100% - 2rem)}.logo{max-height:48px}.timer{margin-bottom:1.5rem}.start-timer{position:fixed;bottom:0;left:0;width:100%}form{display:flex;-moz-flex-direction:column;flex-direction:column;width:100%}form p{margin-bottom:1.5rem;padding:0.5rem 1rem;background:crimson;color:white;font-weight:700;border-radius:5px}input[type="number"]{-moz-flex-grow:2;-ms-flex-grow:2;flex-grow:2;padding:1rem;font-size:1rem;margin-bottom:1rem;border:1px solid #ccc;border-radius:5px}input[type="number"]:focus{outline:0;border-color:#000}.form__toggle{display:-moz-inline-flex;display:inline-flex}input[type="radio"]{display:none}input[type="radio"]:checked+label{background-color:tomato;color:white}label{padding:0.5rem 1rem;cursor:pointer;background-color:white;border:2px solid tomato;color:tomato;font-weight:700;width:50%;text-align:center}label.label--hourly{border-top-left-radius:5px;border-bottom-left-radius:5px}label.label--salary{border-top-right-radius:5px;border-bottom-right-radius:5px}

